import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import PropTypes from 'prop-types';
import axios from 'axios';

// material-ui
import Skeleton from '@mui/material/Skeleton';
import { Box, Card, Grid, Typography, TextField, FormControl, Button, CardContent } from '@mui/material';

// project imports
import SubCard from 'ui-component/cards/SubCard';
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';
import { gridSpacing } from 'store/constant';

// third party
import * as Yup from 'yup';
import { Formik } from 'formik';

// ===============================|| COLOR BOX ||=============================== //

const ColorBox = ({ bgcolor, title, data, dark }) => (
    <>
        <Card sx={{ mb: 3 }}>
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    py: 4.5,
                    bgcolor,
                    color: dark ? 'grey.800' : '#ffffff'
                }}
            >
                {title && (
                    <Typography variant="subtitle1" color="inherit">
                        {title}
                    </Typography>
                )}
                {!title && <Box sx={{ p: 1.15 }} />}
            </Box>
        </Card>
        {data && (
            <Grid container justifyContent="space-between" alignItems="center">
                <Grid item>
                    <Typography variant="subtitle2">{data.label}</Typography>
                </Grid>
                <Grid item>
                    <Typography variant="subtitle1" sx={{ textTransform: 'uppercase' }}>
                        {data.color}
                    </Typography>
                </Grid>
            </Grid>
        )}
    </>
);
// ============================|| FIREBASE - LOGIN ||============================ //

const Stocktaking = ({ ...others }) => {
    const navigate = useNavigate();
    const [isLoading, setLoading] = useState(true);
    const [products, setProducts] = useState([]);
    const [error, setError] = useState('');

    useEffect(() => {
        // CHECK LOGIN
        if (!localStorage.getItem('authToken')) {
            navigate('/login');
            setError('No auth');
        }
        // CHECK LOGIN
    }, [navigate]);

    function logout() {
        localStorage.removeItem('authToken');
        navigate('/login');
    }

    const globalconfig = {
        headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('authToken')}`
        }
    };

    // ðŸ‘‡ðŸ‘‡ FINDALL

    useEffect(() => {
        const getallProducts = async () => {
            try {
                await axios.get(`https://dry-stream-10813.herokuapp.com/api/private/getallproducts`, globalconfig).then((response) => {
                    setProducts(response.data.data);
                    setLoading(false);
                });
            } catch (error) {
                setError(error.message);
            }
        };

        getallProducts();
    }, []);

    const reFetch = async () => {
        setLoading(true);
        await axios.get(`https://dry-stream-10813.herokuapp.com/api/private/getallproducts`, globalconfig).then((response) => {
            setProducts(response.data.data);
            setLoading(false);
        });
    };

    return error ? (
        logout()
    ) : (
        <MainCard title="Color Palette" secondary={<SecondaryAction link="/" />}>
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <SubCard title="Add Products">
                        <Grid container spacing={gridSpacing}>
                            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                                {isLoading ? (
                                    <Card>
                                        <CardContent>
                                            <Grid container direction="column">
                                                <Grid item>
                                                    <Grid container justifyContent="space-between">
                                                        <Grid item>
                                                            <Skeleton variant="rectangular" width={44} height={44} />
                                                        </Grid>
                                                        <Grid item>
                                                            <Skeleton variant="rectangular" width={34} height={34} />
                                                        </Grid>
                                                    </Grid>
                                                </Grid>
                                                <Grid item>
                                                    <Skeleton variant="rectangular" sx={{ my: 2 }} height={40} />
                                                </Grid>
                                                <Grid item>
                                                    <Skeleton variant="rectangular" height={30} />
                                                </Grid>
                                            </Grid>
                                        </CardContent>
                                    </Card>
                                ) : (
                                    <Formik
                                        initialValues={{
                                            brandname: '',
                                            brandtype: '',
                                            brandtypepricertl: 0,
                                            brandtypepricews: 0,
                                            brandcolor: '#e3d4ee'
                                        }}
                                        validate={(values) => {
                                            const errors = {};
                                            if (!values.brandname && !values.brandtype) {
                                                errors.brandname = 'Required';
                                                errors.brandtype = 'Required';
                                                errors.brandtypepricertl = 'Required';
                                                errors.brandtypepricews = 'Required';
                                            }
                                            return errors;
                                        }}
                                        onSubmit={async (values, { setSubmitting }) => {
                                            const config = {
                                                headers: {
                                                    'Content-Type': 'application/json',
                                                    Authorization: `Bearer ${localStorage.getItem('authToken')}`
                                                }
                                            };

                                            const brandname = values.brandname;
                                            const brandtype = values.brandtype;
                                            const brandtypepricertl = values.brandtypepricertl;
                                            const brandtypepricews = values.brandtypepricews;
                                            const brandcolor = values.brandcolor;

                                            try {
                                                alert(
                                                    `${brandname}, ${brandtype}, ${brandtypepricertl}, ${brandtypepricews}, ${brandcolor}`
                                                );
                                                const { data } = await axios.post(
                                                    'https://dry-stream-10813.herokuapp.com/api/private/addproduct',
                                                    { brandname, brandtype, brandtypepricertl, brandtypepricews, brandcolor },
                                                    config
                                                );

                                                if (data === '') {
                                                    setSubmitting(true);
                                                    setLoading(true);
                                                } else {
                                                    setSubmitting(false);
                                                    setLoading(false);
                                                }

                                                reFetch();
                                            } catch (err) {
                                                console.error(err);
                                            }
                                        }}
                                    >
                                        {({
                                            values,
                                            errors,
                                            touched,
                                            handleChange,
                                            handleBlur,
                                            handleSubmit,
                                            isSubmitting
                                            /* and other goodies */
                                        }) => (
                                            <form onSubmit={handleSubmit}>
                                                <FormControl sx={{ m: 4 }}>
                                                    <TextField
                                                        name="brandname"
                                                        helperText="The brand name"
                                                        id="brandname"
                                                        label="Brand Name"
                                                        type="text"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.brandname}
                                                    />
                                                </FormControl>
                                                {errors.brandname && touched.brandname && errors.brandname}
                                                <FormControl sx={{ m: 4 }}>
                                                    <TextField
                                                        name="brandtype"
                                                        helperText="The brand type/variant"
                                                        id="brandtype"
                                                        label="Brand Type"
                                                        type="text"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.brandtype}
                                                    />
                                                </FormControl>
                                                {errors.brandtype && touched.brandtype && errors.brandtype}
                                                <FormControl sx={{ m: 4 }}>
                                                    <TextField
                                                        name="brandtypepricertl"
                                                        helperText="Retail Profit"
                                                        id="brandtypepricertl"
                                                        label="Retail Profit"
                                                        type="number"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.brandtypepricertl}
                                                    />
                                                </FormControl>
                                                {errors.brandtypepricertl && touched.brandtypepricertl && errors.brandtypepricertl}
                                                <FormControl sx={{ m: 4 }}>
                                                    <TextField
                                                        name="brandtypepricews"
                                                        helperText="Wholesale Profit"
                                                        id="brandtypepricews"
                                                        label="Wholesale Profit"
                                                        type="number"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.brandtypepricews}
                                                    />
                                                </FormControl>
                                                {errors.brandtypepricews && touched.brandtypepricews && errors.brandtypepricews}
                                                <FormControl sx={{ m: 4 }}>
                                                    <TextField
                                                        name="brandcolor"
                                                        helperText="Color identifier for this variant"
                                                        id="brandcolor"
                                                        label="Variant Color"
                                                        type="color"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.brandcolor}
                                                    />
                                                </FormControl>
                                                {errors.brandcolor && touched.brandcolor && errors.brandcolor}
                                                <Button type="submit" disabled={isSubmitting}>
                                                    Submit
                                                </Button>
                                            </form>
                                        )}
                                    </Formik>
                                )}
                            </Grid>
                        </Grid>
                    </SubCard>
                </Grid>
                <Grid item xs={12}>
                    <SubCard title="Products">
                        <Grid container spacing={gridSpacing}>
                            {products !== undefined ? (
                                products.map((val, key) => (
                                    <Grid item xs={12} sm={6} md={4} lg={2} key={key}>
                                        <ColorBox bgcolor={val.brandcolor} data={{ label: '', color: '' }} title={val.brandtype} dark />
                                    </Grid>
                                ))
                            ) : (
                                <Grid item xs={12} sm={12} md={12} lg={12}>
                                    <ColorBox bgcolor="error.main" data={{ label: '', color: '' }} title="Add products" />
                                </Grid>
                            )}
                        </Grid>
                    </SubCard>
                </Grid>
            </Grid>
        </MainCard>
    );
};

export default Stocktaking;
